  <div class="space-y-12">
    <!-- Hero (single image, no slider) -->
    <section class="relative overflow-hidden rounded-2xl bg-gray-100">
      <div class="relative h-[260px] sm:h-[320px]">
        <NuxtImg
          src="/img/4884273.jpg"
          class="w-full h-full object-cover"
          format="webp"
          alt="Kia / Hyundai VIN to PIN"
        />
        <div class="absolute inset-0 bg-gradient-to-r from-black/50 via-black/30 to-transparent" />

        <div class="absolute inset-0 flex items-center">
          <div class="px-6 sm:px-10">
            <div class="max-w-xl bg-white/90 backdrop-blur rounded-2xl p-6 sm:p-8 shadow-md">
              <h1 class="text-2xl sm:text-3xl font-bold mb-3">
                {{ $t('hero.headline') || 'Why choose our PIN Code platform?' }}
              </h1>
              <ul class="text-sm text-gray-700 space-y-1 mb-5">
                <li>• {{ $t('hero.points.instant') || 'Instant calculation 24/7' }}</li>
                <li>• {{ $t('hero.points.noRenewal') || 'No renewal fees · tokens never expire' }}</li>
                <li>• {{ $t('hero.points.easy') || 'Easy to Use' }}</li>
              </ul>
              <div class="flex gap-3">
                <NuxtLink to="/new-lookup" class="btn">
                  {{ $t('btn.newLookup') || 'New VIN Lookup' }}
                </NuxtLink>
                <NuxtLink to="/buy" class="btn btn-ghost">
                  {{ $t('btn.buyTokens') || 'Buy Tokens' }}
                </NuxtLink>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!-- Supported Brands Slider -->
    <section class="space-y-4">
      <div class="text-center">
        <h2 class="text-2xl sm:text-2xl font-extrabold tracking-tight">
          {{ $t('home.supportedBrands') || 'Supported Brands' }}
        </h2>
      </div>

      <div class="relative">
        <div
          ref="brandRail"
          class="overflow-x-auto scroll-smooth [-ms-overflow-style:none] [scrollbar-width:none]"
        >
          <div class="flex gap-6 items-center justify-start px-2 py-2 w-60">
            <div v-for="b in brands" :key="b.key" class="brand-pill">
              <NuxtImg
                v-if="b.logo"
                :src="b.logo"
                :alt="b.name"
                format="webp"
                class="h-10 w-auto object-contain"
              />
              <span v-else class="text-lg font-semibold tracking-wide">
                {{ b.name }}
              </span>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!-- Token Packs -->
    <section v-if="allPacks.length" class="space-y-5">
      <div class="flex items-baseline justify-between">
        <h3 class="text-xl sm:text-2xl font-bold">
          {{ $t('home.tokenPacks') || 'Token Packs' }}
        </h3>
      </div>

      <div class="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
        <article
          v-for="p in allPacks"
          :key="p.id"
          class="pack-card flex flex-col relative group"
        >
          <!-- FULL CARD LINK -->
          <NuxtLink
            :to="`/product/${p.slug || p.id}`"
            class="absolute inset-0 z-0"
            :aria-label="`Open ${p.name}`"
          />

          <!-- Header -->
          <header class="pack-card__header pack-card__header--dark text-center z-10">
            <h4 class="text-sm sm:text-md font-bold tracking-tight text-white leading-snug">
              {{ p.name }}
            </h4>
          </header>

          <!-- Body (shorter spacing) -->
          <div class="pack-card__body flex-1 flex flex-col z-10">
            <div class="text-center">
              <span class="inline-block text-red-700 text-base font-bold">
                {{ p.tokens }} {{ $t('tokens') || 'tokens' }}
              </span>
            </div>

            <NuxtImg
              v-if="p.image_url"
              :src="p.image_url"
              format="webp"
              class="pack-image"
              :modifiers="{ fit: 'contain', background: 'white' }"
              alt=""
            />

            <p v-if="p.sku" class="text-green-600 text-center font-semibold text-xs mb-2">
              SKU: {{ p.sku }}
            </p>

            <!-- Tier table -->
            <div
              v-if="p.pricing_tiers?.length"
              class="tier-box mb-3"
              @click.stop
            >
              <table class="tier-table">
                <thead>
                  <tr>
                    <th class="text-left">{{ $t('common.quantity') || 'Quantity' }}</th>
                    <th class="text-right">{{ $t('common.price') || 'Price' }}</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(r, i) in normalizedTiers(p)"
                    :key="i"
                    :class="[
                      i === activeTierIndex(p, qty[p.id]) ? 'tier-row--active' : '',
                    ]"
                  >
                    <td>
                      <span v-if="r.max_qty">{{ r.min_qty }}–{{ r.max_qty }}</span>
                      <span v-else>{{ r.min_qty }}+</span>
                    </td>
                    <td class="text-right">
                      <template v-if="r.sale_price_usd">
                        <span class="font-bold text-red-700">
                          ${{ Number(r.sale_price_usd).toFixed(2) }}
                        </span>
                        <span class="text-gray-400 line-through ml-1">
                          ${{ Number(r.price_usd).toFixed(2) }}
                        </span>
                      </template>
                      <template v-else>
                        <span class="font-semibold text-gray-900">
                          ${{ Number(r.price_usd).toFixed(2) }}
                        </span>
                      </template>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>

            <!-- Big price -->
            <div class="text-center mb-3">
              <template v-if="currentStrikePrice(p, qty[p.id])">
                <div class="text-2xl font-black text-gray-900 leading-none">
                  ${{ currentUnitPrice(p, qty[p.id]).toFixed(2) }}
                </div>
                <div class="text-xs text-gray-400 line-through mt-1">
                  ${{ currentStrikePrice(p, qty[p.id])!.toFixed(2) }}
                </div>
              </template>
              <template v-else>
                <div class="text-xl font-black text-red-500 leading-none">
                  ${{ currentUnitPrice(p, qty[p.id]).toFixed(2) }}
                </div>
              </template>
            </div>

            <!-- CTA bottom -->
            <div class="mt-auto" @click.stop>
              <div class="flex items-center gap-2">
                <input
                  v-model.number="qty[p.id]"
                  type="number"
                  min="1"
                  class="w-14 rounded-lg border px-2 py-2 text-center text-sm"
                  @click.stop
                />
                <button
                  class="btn w-full"
                  @click.stop="addToCartCard(p)"
                >
                  {{ $t('btn.addToCart') || 'Add To Cart' }}
                </button>
              </div>
            </div>
          </div>
        </article>
      </div>
    </section>

    <div v-if="errorMsg" class="text-red-600 text-sm">
      {{ errorMsg }}
    </div>
  </div>
</template>

<script setup lang="ts">
type PricingTier = {
  min_qty: number
  max_qty?: number | null
  price_usd: number
  sale_price_usd?: number | null
}

type TokenPack = {
  id: number
  name: string
  slug?: string | null
  sku?: string | null
  tokens: number
  service_category_id?: number | null
  price_usd: number | string
  sale_price_usd?: number | string | null
  pricing_tiers?: PricingTier[] | null
  image_url?: string | null
}

const { locale, t } = useI18n()
const cart = useCart()
const { show } = useAddedToast()
const qty = reactive<Record<number, number>>({})

function normalizedTiers(p: TokenPack): PricingTier[] {
  return (p.pricing_tiers || [])
    .slice()
    .sort((a, b) => (a.min_qty || 1) - (b.min_qty || 1))
}

function activeTierIndex(p: TokenPack, qtyNum?: number): number {
  const q = Math.max(1, Number(qtyNum || 1))
  const tiers = normalizedTiers(p)
  for (let i = 0; i < tiers.length; i++) {
    const tr = tiers[i]
    const max = tr.max_qty ?? Number.POSITIVE_INFINITY
    if (q >= (tr.min_qty || 1) && q <= max) return i
  }
  return -1
}

function currentUnitPrice(p: TokenPack, qtyNum?: number): number {
  const q = Math.max(1, Number(qtyNum || 1))
  const tiers = normalizedTiers(p)
  for (const tr of tiers) {
    const max = tr.max_qty ?? Number.POSITIVE_INFINITY
    if (q >= (tr.min_qty || 1) && q <= max) {
      return Number(tr.sale_price_usd ?? tr.price_usd ?? 0)
    }
  }
  return Number((p.sale_price_usd ?? p.price_usd) || 0)
}

function currentStrikePrice(p: TokenPack, qtyNum?: number): number | null {
  const q = Math.max(1, Number(qtyNum || 1))
  const tiers = normalizedTiers(p)
  for (const tr of tiers) {
    const max = tr.max_qty ?? Number.POSITIVE_INFINITY
    if (q >= (tr.min_qty || 1) && q <= max) {
      return tr.sale_price_usd ? Number(tr.price_usd) : null
    }
  }
  return p.sale_price_usd ? Number(p.price_usd) : null
}

function addToCartCard(p: TokenPack) {
  const q = Math.max(1, Number(qty[p.id] || 1))
  const unit = currentUnitPrice(p, q)

  cart.add({
    product_id: p.id,
    product_type: 'token-pack',
    slug: p.slug || String(p.id),
    sku: p.sku || p.slug || String(p.id),
    name: p.name,
    price: unit,
    qty: q,
    image: p.image_url || null,
    meta: {
      tokens: p.tokens,
      base_price_usd: Number(p.price_usd || 0),
      sale_price_usd: p.sale_price_usd != null ? Number(p.sale_price_usd) : null,
      pricing_tiers: p.pricing_tiers || null,
    },
  })

  show({
    img: p.image_url || null,
    title: p.name,
    sku: p.sku || p.slug || String(p.id),
  })
}

/* API */
const { get } = useApi()
const fetchPacks = () => get<TokenPack[]>('/token-packs')

const { data: packs, error: packErr } = await useAsyncData(
  'home-packs',
  fetchPacks,
  { server: true, immediate: true }
)

watchEffect(() => {
  if (packs.value) {
    packs.value.forEach(p => {
      if (!qty[p.id]) qty[p.id] = 1
    })
  }
})

watch(locale, () => refreshNuxtData(['home-packs']))

const allPacks = computed(() => packs.value ?? [])
const errorMsg = computed(() => {
  const e: any = packErr.value
  return e?.statusMessage || e?.message || ''
})

const brands = [
  { key: 'kia', name: 'KIA', logo: '/img/brands/kia.svg' },
  { key: 'hyundai', name: 'HYUNDAI', logo: '/img/brands/hyundai.svg' },
  { key: 'toyota', name: 'TOYOTA', logo: '/img/brands/toyota.svg' },
]

const brandRail = ref<HTMLDivElement | null>(null)
function scrollBrands(dir: 1 | -1) {
  const el = brandRail.value
  if (!el) return
  const itemW = el.querySelector('.brand-pill')?.clientWidth ?? 180
  el.scrollBy({ left: dir * (itemW + 24) * 2, behavior: 'smooth' })
}

useHead({
  title: t('seo.title') || 'Immobilizer PIN Code Calculator',
  meta: [{ name: 'description', content: t('seo.desc') || 'VIN→PIN and token packs.' }],
})
</script>

